@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@1,300&display=swap);
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(250, 244, 224);
}

p.welcomeText {
  text-align: center;
}

h1 {
  text-align: center;
}

.container {
  width: 100vw;
  height: 100%;
  background-color: antiquewhite;
}

header {
  height: 150px;
  background: linear-gradient(90deg, #f60303, #fdf903);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 0.5rem;
}

.logo {
  margin: 1rem;
  padding: 1rem;
}

main {
  padding: 0.5rem;
}

nav ul li {
  display: inline-block;
  margin: 0 1rem;
}

p nav li {
  font-family: "Poppins", sans-serif;
}

nav {
  font-size: 1.5rem;
}

h1 {
  font-family: "Lobster", cursive;
}

header a {
  text-decoration: none;
  color: #333;
}

header a:hover {
  color: white;
  text-decoration: underline;
}

footer {
  min-height: 80px;
  width: 100%;
  background: linear-gradient(90deg, #fdf903, #f60303);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  bottom: 0;
}

* {
  box-sizing: border-box;
}

:root {
  --primary-color: white;
  --secondary-color: rgb(246, 3, 3);
  --black-color: #111;
  --orange: orangered;
}

body {
  background-color: rgb(255, 242, 220);
  font-family: "Poppins", sans-serif;
}

.container-recipes {
  padding: 40px;
  margin: 0 auto;
  max-width: 100%;
  width: 800px;
}

div.single.container {
  margin: 20px;
}

.btn-recipes,
button.backToRecipe {
  background-color: orangered;
  background-color: var(--orange);
  color: white;
  color: var(--primary-color);
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: 600;
  padding: 20px 35px;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  box-shadow: 0 5px 5px rgb(95, 93, 73);
}

a.btn-recipes,
button.backToRecipe {
  text-decoration: none;
}

a.btn-recipes:hover {
  color: yellow;
}

button.backToRecipe:hover {
  color: yellow;
}

.recipes {
  background-color: white;
  background-color: var(--primary-color);
  display: -webkit-flex;
  display: flex;
  box-shadow: 0 10px 10px rgb(95, 93, 73);
}

div.single.container {
  background-color: white;
  background-color: var(--primary-color);
  box-shadow: 0 10px 10px rgb(95, 93, 73);
  margin-bottom: 100px;
  margin-top: 20px;
}

.recipes-img {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  overflow: hidden;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.recipes-img img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.recipes-details {
  padding: 30px;
  position: relative;
  -webkit-flex: 2 1;
          flex: 2 1;
}

.recipes-title {
  margin: 0 0 20px;
  color: rgb(246, 3, 3);
  color: var(--secondary-color);
}

h3 {
  color: rgb(246, 3, 3);
  color: var(--secondary-color);
  padding: 20px;
}

.recipes-instruction {
  font-size: 14px;
}
.recipes-info {
  display: -webkit-flex;
  display: flex;
  list-style-type: none;
  padding: 0;
}

.recipes-info li {
  color: grey;
  font-size: 12px;
  font-weight: 300;
  margin-right: 50px;
}
.recipiesLists,
.addForm {
  padding-bottom: 100px;
}
.mail-info li strong {
  font-size: 18px;
  display: block;
}

.recipes-details .btn-recipes {
  position: absolute;
  right: -10px;
  bottom: -10px;
}

.search {
  padding: 20px;
  justify-items: right;
  display: grid;
}

.rounded {
  float: left;
  margin: 10px 30px 10px 10px;
}

.form,
.contactMe {
  background: white;
  color: black;
  border-radius: 5px;
  max-width: 100%;
  width: 1200px;
  margin: 30px auto;
  box-shadow: 0 10px 10px rgb(95 93 73);
  padding: 30px;
}

.contactMe {
  margin-top: 10%;
  margin-bottom: 30%;
  width: 800px;
  padding: 50px;
}

label {
  width: 150px;
  display: inline-block;
}

form div {
  margin: 1rem;
}

p {
  margin: 20px;
}

#cookTime.form-control {
  width: 20%;
}

#description.form-control {
  width: 100%;
  height: 300px;
}
#quantity.form-control {
  width: 30%;
}
#recipeInstructions.form-control {
  width: 100%;
  height: 300px;
}

div.col {
  margin: 5px;
}
.addMore {
  margin: 20px;
  color: orangered;
  border-color: orangered;
}

.addMore:hover {
  background-color: orangered;
  border-color: orangered;
}

.post {
  background-color: orangered;
  border-color: orangered;
}

.post:hover {
  background-color: orange;
  border-color: orange;
}

